<script>
import Address from "../components/Address.vue";

export default {
  name: "App",

  components: {
    Address,
  },

  data: () => ({
    valid: true,
    name: "",
    IdNum: "",
    birthday: "",
    phone: "",
    educate: "",
    educateList: ["小學以下", "國高中畢業", "大專院校畢業或以上"],
    job: "",
    classWant: "",
    classWantList: ["入門課程", "高階課程", "半年會員", "一年保證班"],
    source: "",
    invest: "",
    census: "",
    address: "",
    rules: {
      name: [
        (v) => !!v || "請輸入姓名",
        // (v) => /.*^[\u4e00-\u9fa5]+.*$/.test(v) || "請輸入中文",
        // (v) => /^[\u4e00-\u9fa5]*[\u4e00-\u9fa5]$/.test(v) || "請輸入中文",
      ],
      birthday: [(v) => !!v || "請輸入生日"],
      IdNum: [
        (v) => !!v || "請輸入身分證",
        (v) => /^[A-Z]{1}[1-2]{1}[0-9]{8}$/.test(v) || "請輸入正確的身分證",
      ],
      phone: [
        (v) => !!v || "請輸入手機",
        (v) => /^[09]{2}[0-9]{8}$/.test(v) || "請輸入正確的手機",
      ],
      educate: [(v) => !!v || "請選擇教育程度"],
      job: [
        (v) => !!v || "請輸入職業",
        // (v) => /^[\u4e00-\u9fa5]*[\u4e00-\u9fa5]$/.test(v) || "請輸入中文",
      ],
      classWant: [(v) => !!v || "請選擇課程"],
    },
  }),

  methods: {
    getCensus(census) {
      this.census = census;
    },

    m_getCensus(census) {
      this.census = census;
    },

    getArddess(address) {
      this.address = address;
    },

    m_getArddess(address) {
      this.address = address;
    },

    sendCheck() {
      this.$refs.form.validate();

      if (this.$refs.form.validate() === false) return;

      if (this.$refs.form.validate() === true) {
        this.$router.push({ name: "Check" });

        const params = new URLSearchParams();
        params.append("A_Field", this.name);
        params.append("B_Field", this.birthday);
        params.append("C_Field", this.IdNum);
        params.append("D_Field", this.phone);
        params.append("E_Field", this.educate);
        params.append("F_Field", this.job);
        params.append("G_Field", this.census);
        params.append("H_Field", this.address);
        params.append("I_Field", this.classWant);
        params.append("J_Field", this.source);
        params.append("K_Field", this.invest);

        const api = `http://www.mongo889.com/api/line/lucky_post`;
        this.$http
          .post(api, params)
          .then((result) => {
            console.log(result);
          })
          .catch((err) => {
            console.error(err);
          });
      }
    },

    m_sendCheck() {
      this.$refs.m_form.validate();

      if (this.$refs.m_form.validate() === false) return;

      if (this.$refs.m_form.validate() === true) {
        this.$router.push({ name: "Check" });

        const params = new URLSearchParams();
        params.append("A_Field", this.name);
        params.append("B_Field", this.birthday);
        params.append("C_Field", this.IdNum);
        params.append("D_Field", this.phone);
        params.append("E_Field", this.educate);
        params.append("F_Field", this.job);
        params.append("G_Field", this.census);
        params.append("H_Field", this.address);
        params.append("I_Field", this.classWant);
        params.append("J_Field", this.source);
        params.append("K_Field", this.invest);

        const api = `http://www.mongo889.com/api/line/lucky_post`;
        this.$http
          .post(api, params)
          .then((result) => {
            console.log(result);
          })
          .catch((err) => {
            console.error(err);
          });
      }
    },
  },
};
</script>

