<template>
  <div>
    <div class="d-md-flex d-none">
      <div class="input-width">
        <v-select
          class="px-4"
          v-model="countie"
          :items="counties"
          item-text="countie"
          dense
          solo
          background-color="rgba(0,0,0,.7)"
          return-object
          :rules="rules.countie"
          required
          @change="selectedCountie()"
        >
          <template v-slot:label>
            <p class="three_text-label">
              <span class="red--text">*</span> 縣市
            </p>
          </template></v-select
        >
      </div>
      <div class="input-width">
        <v-select
          class="px-4"
          v-model="district"
          :items="districts"
          item-text="district"
          dense
          solo
          background-color="rgba(0,0,0,.7)"
          return-object
          :disabled="openDistrict"
          :rules="rules.district"
          required
          @change="selectedDistrict()"
        >
          <template v-slot:label>
            <p class="three_text-label">
              <span class="red--text">*</span> 區鄉鎮
            </p>
          </template></v-select
        >
      </div>
      <div class="input-width">
        <v-text-field
          v-model="road"
          class="pl-4"
          background-color="rgba(0,0,0,.7)"
          solo
          dense
          :rules="rules.road"
          required
          @change="sendAddress()"
        >
          <template v-slot:label>
            <p class="three_text-label">
              <span class="red--text">*</span> 地址
            </p>
          </template></v-text-field
        >
      </div>
    </div>

    <div class="d-block d-md-none">
      <div class="d-flex">
        <v-select
          class="px-2"
          v-model="countie"
          :items="counties"
          item-text="countie"
          dense
          solo
          background-color="rgba(0,0,0,.7)"
          return-object
          :rules="rules.countie"
          required
          @change="selectedCountie()"
        >
          <template v-slot:label>
            <p class="three_text-label">
              <span class="red--text">*</span> 縣市
            </p>
          </template></v-select
        >
        <v-select
          class="px-2"
          v-model="district"
          :items="districts"
          item-text="district"
          dense
          solo
          background-color="rgba(0,0,0,.7)"
          return-object
          :disabled="openDistrict"
          :rules="rules.district"
          required
          @change="selectedDistrict()"
        >
          <template v-slot:label>
            <p class="three_text-label">
              <span class="red--text">*</span> 區鄉鎮
            </p>
          </template></v-select
        >
      </div>

      <div class="d-flex">
        <v-text-field
          v-model="road"
          class="px-2"
          background-color="rgba(0,0,0,.7)"
          solo
          dense
          :rules="rules.road"
          required
          @change="sendAddress()"
        >
          <template v-slot:label>
            <p class="three_text-label">
              <span class="red--text">*</span> 地址
            </p>
          </template></v-text-field
        >
      </div>
    </div>
  </div>
</template>

<script>
import data from "../../data/location";

export default {
  name: "App",

  data: () => ({
    location: data,
    countie: "",
    counties: [],
    openDistrict: true,
    district: "",
    districts: [],
    postalNum: "",
    road: "",
    rules: {
      countie: [(v) => !!v || "請選擇縣市"],
      district: [(v) => !!v || "請選擇區鄉鎮"],
      road: [(v) => !!v || "請輸入地址"],
    },
  }),

  created() {
    this.changeData();
  },

  methods: {
    changeData() {
      this.counties = data.counties.map((item, index) => {
        return (item = {
          countie: item,
          index: index,
        });
      });
    },

    selectedCountie() {
      if (this.countie === "") return;

      this.openDistrict = false;

      this.districts = data.districts[this.countie.index][0];

      for (let index = 0; index < this.districts.length; index++) {
        this.districts[index] = {
          index: index,
          district: this.districts[index],
        };
      }
    },

    selectedDistrict() {
      if (this.district === "") return;

      this.postalNum =
        data.districts[this.countie.index][1][this.district.index];
    },

    sendAddress() {
      let census = this.countie.countie;
      let address =
        this.postalNum +
        this.countie.countie +
        this.district.district +
        this.road;
      this.$emit("postCensus", census);
      this.$emit("postArddess", address);
    },
  },
};
</script>

<style lang="css" scoped>
.input-width {
  width: 33%;
}
</style>
